.root {
  height: 100%;
}

.App {
  text-align: center;
  height: 100%;
}

.container {
  width: calc(100%);
  max-width: 800px;
  margin: 0;
  overflow: hidden;
  box-sizing: border-box;
  height: 100%;
}
.container-inner {
  width: 200%;
  display: flex;
  align-items: stretch;
  box-sizing: border-box;
  height: 100%;
}
